.nav-menu{
    display: flex;
    
}
.nav-menu a {
    padding: 0 1rem;
    font-size: 1.2rem;
    font-weight: 500;
}
.header{
    display: flex;
    justify-content:space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;

}
.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
}
.hamburger{
    display: none;
}
@media screen and (max-width : 1040px){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -3;
        transition: 0.3s;
    }
    .nav-menu.active{
        left: -100%;
    }
    .nav-menu a{
        padding: 1rem 0;
        font-size: 2rem;
    }
    .hamburger{
        cursor: pointer;
        display: initial;
    }
}